// Общие настройки

*{
    transition: 0.2s linear;
}
.container {
    max-width: 1220px;
    padding: 0px 10px;
    margin: 0 auto;
}

.site-container {
    height: 100%;
}



.site-container {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.page-container {
    flex: 1 0 auto;
    padding-top: 50px;
}

.footer {
    flex: 0 0 auto;
}

@media(max-width: 768px){
    .page-container{
        padding-top: 10px;
    }
}

.title{
    font-weight: 400;
    font-size: 38px;
    line-height: 46px;
    color: #303133;
}

.section{
    padding-top: 50px;
    border-top: 1px solid #E1EAFF;
    margin-top: 40px;
}

.col-3{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}

.p-17{
    font-weight: 400;
    font-size: 17px;
    line-height: 32px;
    color: #303133;
}

.p-21{
    font-weight: 400;
    font-size: 21px;
    line-height: 32px;
    color: #303133;
}

@media(max-width: 768px){
    .p-17{
        font-size: 13px;
        line-height: 200%;
    }
    .p-21{
        font-size: 15px;
        line-height: 160%;
    }
}

.col-4{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
}

@media(max-width: 991px){
    .col-3{
        grid-template-columns: repeat(2, 1fr);
    }

    .col-4{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
}

@media(max-width: 580px){
    .col-3{
        grid-template-columns: repeat(1, 1fr);
    }

    .col-4{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
    }
}

@media (max-width: 768px){
    .section{
        padding-top: 25px;
        margin-top: 20px;
    }
    .title{
        font-size: 24px;
        line-height: 120%;
    }   
}


