.button{
    display: inline-block;
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;
    color: #FFFFFF;
    padding: 16px 26px;
    background: #3A75FF;
    border: none;
    outline: none;
    appearance: none;
    border-radius: 5px;
    cursor: pointer;
    text-transform: uppercase;
    font-family: 'Montserrat';
    transition: background 0.2s linear;
    &:hover{
        background: #3062d4;
    }

    &:focus{
        background: #3062d4;
    }

    &:active{
        background: #244eb1;
    }
}


@media(max-width: 768px){
    .button{
        padding: 13px 24px;
        font-size: 11px;
    }
}