.header {
    .top-header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 40px 0px;
    }

    

    form {
        display: flex;
        align-items: center;
        margin-left: 20px;

        svg {
            margin-right: 10px;
            border-radius: 4px;
        }

        select {
            font-weight: 400;
            font-size: 12px;
            line-height: 15px;
            color: #484848;
            position: relative;
            font-family: 'Montserrat';
            cursor: pointer;

            &:hover {
                opacity: 0.7;
            }
        }
    }


    .col {
        display: flex;
        align-items: center;
    }

    .menu {
        background: #F5F8FF;
        padding: 17px 0px;

        &__inner {
            ul {
                display: flex;

                li {
                    &:not(:last-child) {
                        margin-right: 50px;
                    }
                }

                a {
                    font-style: normal;
                    font-weight: 500;
                    font-size: 15px;
                    line-height: 18px;
                    color: #303133;

                    &:hover {
                        opacity: 0.7;
                    }
                }

                a.active {
                    font-weight: 700;
                    position: relative;

                    &::before {
                        content: '';
                        position: absolute;
                        width: 20px;
                        height: 6px;
                        border-top-right-radius: 10px;
                        border-top-left-radius: 10px;
                        background: #3A75FF;
                        bottom: -17px;
                        left: 0;
                    }
                }
            }
        }
    }


    .burger{
        width: 30px;
        height: 30px;
        border-radius: 30px;
        background: #F5F8FF;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        span{
            height: 2px;
            width: 16px;
            border-radius: 2px;
            display: block;
            background: #303133;
            &:not(:last-child){
                margin-bottom: 3px;
            }
        }
    }

    .burger.open{
        span{
            &:nth-child(2){
                display: none;
            }
            &:nth-child(1){
                transform: rotate(45deg) translateY(2px) translateX(1px);
            }
            &:nth-child(3){
                transform: rotate(-45deg) translateX(2px) translateY(-2px)
            }
        }
    }

}

// mobile-menu

.header__inner.open{
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background: #F5F8FF;
    z-index: 99;
}

.select-wrap {
    position: relative;
    padding-right: 16px;

    &::before {
        content: '';
        position: absolute;
        display: block;
        background: url('../img/arrow-down.svg');
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        width: 10px;
        height: 6px;
        right: 0px;
        top: 8px;

    }
}

.phone {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 100px;
    background: #F5F8FF;
    padding-left: 25px;
    font-size: 18px;
    line-height: 22px;
    color: #000000;

    svg {
        margin-left: 15px;

    }

    &:hover {
        background: #e2eafc;
        

        svg {
            transform: scale(1.05);
        }
    }
}


@media(min-width: 768px) {
    .desktop-hidden {
        display: none !important;
    }
}

@media(max-width: 768px) {
    .header {
        min-height: 74px;
        .top-header {
            padding: 20px 0px;
            .col {
                &:nth-child(2) {
                    display: none;
                }
            }
            .logo{
                svg{
                    max-width: 152px;
                    height: 30px;
                }
            }
        }
    }

    .header .menu {
        width: 100%;
        max-height: 100vh;
        height: 100%;
        overflow-y: auto;
        position: absolute;
        top: 75px;
        left: 0px;
        padding-top: 0;
        opacity: 0;
        visibility: hidden;
        &__inner {
            ul {
                flex-direction: column;
                text-align: center;
                align-items: center;

                li {
                    margin-right: 0 !important;
                    margin-bottom: 40px;

                    a {
                        font-size: 18px;
                        font-weight: 400;
                    }

                    a.active {
                        &::before {
                            bottom: -10px;
                            width: 100%;
                        }
                    }
                }
            }
            padding-bottom: 100px;
            padding-top: 25px;
        }

        .phone{
            max-width: 200px;
            margin: 0 auto;
            margin-top: 60px;
            background: #ECF2FF;
            font-size: 18px;
        }

        .language-switcher{
            margin: 0 auto;
            max-width: 105px;
            margin-top: 30px;
        }

    }

    .header__inner.open .menu{
        visibility: visible;
        opacity: 1;
    }
}