// Стили

.main-screen__inner{
    .banner{
        width: 100%;
        height: 315px;
        margin-bottom: 50px;
        margin-top: 0;
        position: relative;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 5px;

        }
        .play-btn{
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            &:hover{
                opacity: 0.5;
            }
        }
    }

    .content{
        p{
            
            padding-top: 17px;
            padding-bottom: 30px;
        }
    }
}


@media (max-width: 768px){
    .main-screen__inner{
        .banner{
           margin-bottom: 30px;
           height: 170px;
           .play-btn{
               svg{
                   width: 30px;
                   height: 26px;
               }
           }
        }
        .content{
            p{
                padding-top: 10px;
                padding-bottom: 20px;
            }
        }
    }
}








.products{
    &__slider{
        width: 100%;
        height: 100%;
        display: flex;
        
        &-wrapper{
            margin-top: 30px;
            width: 100%;
            height: 100%;
            margin-bottom: 50px;
        }

        .products-slide{
            flex-shrink: 0;
        }
    }

    &-img{
        max-height: 330px;
        height: auto;
        width: 100%;
        background: #F5F8FF;
        padding: 50px 14px;
        border-radius: 5px;
        display: flex;
        justify-content: center;
        align-items: center;
        img{
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }
    &-descr{
       
        color: #303133;
        padding-top: 20px;
        .darker{
            font-weight: 500;
        }
    }

    .btns-wrap{
        display: flex;
        align-items: center;
        justify-content: space-between;
        .slider-nav{
            display: flex;
            .swiper-button-prev{
                margin-right: 30px;
            }
            .swiper-button-prev, .swiper-button-next{
                width: 25px;
                height: 25px;
                display: flex;
                justify-content: center;
                align-items: center;
                cursor: pointer;
                &:hover{
                    opacity: 0.5;
                }
            }
            .swiper-button-disabled{
                opacity: 0.2;
            }
        }
    }
}

@media (max-width: 768px){
    .products{
        &-descr{
            font-size: 15px;
            padding-top: 10px;
        }
        &__slider-wrapper{
            max-width: 200px;
            margin-bottom: 20px;
        }

        .slider-nav{
            .swiper-button-prev{
                margin-right: 15px !important;
            }
        }
    }
}



.benefits{
    .benefits-grid{
        margin-top: 40px;
        margin-bottom: 100px;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 50px;
    }

    .progress{
        width: 154px;
        height: 154px;
        border-radius: 100%;
        border: 2px solid #F5F8FF;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        &-line{
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: calc(100% + 8px);
            height: calc(100% + 8px);
            border-radius: 100%;
            border: 6px solid #3A75FF;
            clip-path: polygon(0 40%, 100% 40%, 100% 100%, 0% 100%);
        }
    }
    .benefits-descr{
        position: relative;
        &::before{
            content: '';
            position: absolute;
            width: 167px;
            height: 17px;
            background: #DFE9FF;
            top: 27px;
            left: 0;
            z-index: 1;
        }
        p{
            max-width: 340px;
            padding-top: 20px;
            padding-bottom: 12px;
            position: relative;
            z-index: 5;
            color: #303133;
        }
    }

    .percent{
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 600;
        font-size: 28px;
        line-height: 34px;
        color: #303133;
    }
    
}

@media(max-width: 991px){
    .benefits{
        .benefits-grid{
            grid-template-columns: repeat(2, 1fr);
        }
    }
}

@media(max-width: 768px){
    .benefits{
        &-grid{
            gap: 45px !important;
            margin-bottom: 50px !important;
        }
        .progress{
            width: 115px;
            height: 115px;
            svg{
                width: 36px;
                height: 36px;
            }
        }
        .benefits-descr{
            p{
                font-size: 15px;
                line-height: 160%;
            }
            &::before{
                width: 110px;
            }
        }
        .percent{
            font-size: 24px;
        }
    }
}



