.history{
    padding-top: 80px;
    &-slider{
        width: 100%;
        height: 100%;
        overflow: hidden;
        padding-top: 40px;
        position: relative;
        &-wrapper{
            width: 100%;
            height: 100%;
            display: flex;

        }
        &-slide{
            flex-shrink: 0;
            display: flex;
            opacity: 0 !important;
            .slide-img{
                max-width: 448px;
                width: 100%;
            }
            .slide-text{
                padding-left: 40px;
                position: relative;
                &::before{
                    content: '';
                    position: absolute;
                    width: 250px;
                    height: 17px;
                    background: #DFE9FF;
                    top: 77px;
                    left: 36px;
                    z-index: 1;
                }
                h3{
                    font-weight: 600;
                    font-size: 38px;
                    line-height: 46px;
                    color: #3A75FF;
                }


                p{
                   
                    padding-top: 17px;
                    max-width: 685px;
                    position: relative;
                    z-index: 5;
                }
            }
           
        }
        .swiper-slide-active{
            opacity: 1 !important;
        }
        .swiper-pagination{
            display: flex;
            position: absolute;
            bottom: 0;
            left: 43.7%;
            transform: translateX(-50%);
        }
        .swiper-pagination-bullet{
            display: block;
            width: 8px;
            height: 8px;
            border-radius: 8px;
            background: #D6E0F8;
            cursor: pointer;
            &:not(:last-child){
                margin-right: 6px;
            }
        }
        .swiper-pagination-bullet-active{
            background: #3A75FF;
            width: 16px;
        }
    }
}

@media(max-width: 991px){
    .history{
        padding-top: 30px;
        &-slider{
            padding-top: 30px;
           &-slide{
            flex-direction: column;
            .slide-text{
                padding-left: 0;
                padding-top: 40px;
                padding-bottom: 40px;
                &::before{
                    left: 0;
                    top: 120px;
                }
                p{
                    max-width: 100%;
                }
            }
            .slide-img{
                max-width: 100%;
                img{
                    width: 100%;
                }
            }
           }
        }

        .swiper-pagination{
            left: 0;
            bottom: 0;
            transform: translateX(0);
        }

    }
}

@media(max-width: 768px){
    .history{
            &-slider{
            &-slide{
