}



.media{
    padding-top: 150px;
    &-slider{
        width: 100%;
        height: 100%;
        padding-top: 40px;

        &-wrapper{
            display: flex;
            width: 100%;
            height: 100%;

        }
        &-slide{
            flex-shrink: 0;
            &:hover{
                .slide-img img{
                    opacity: 1;
                    filter: grayscale(0);
                }

                .slide-text{
                    text-decoration-line: underline;
                }
            }
            .slide-img{
                width: 100%;
                max-height: 220px;
                height: 100%;
                padding: 70px 100px;
                background: #F5F8FF;
                border-radius: 5px;
                margin-bottom: 20px;
                img{
                    width: 100%;
                    height: 100%;
                    object-fit: contain;
                    filter: grayscale(1);
                    opacity: 0.2;
                }
            }

            .slide-title{
                font-weight: 500;
                font-size: 21px;
                line-height: 32px;
                color: #303133;
                margin-bottom: 10px;
            }

            .slide-text{
               
                color: #303133;
                position: relative;
                padding-left: 50px;
                &::after{
                    content: '';
                    display: block;
                    position: absolute;
                    width: 35px;
                    height: 35px;
                    background: url('../img/quotes.svg');
                    background-repeat: no-repeat;
                    background-size: cover;
                    background-position: center;
                    top: 0;
                    left: 0;
                }
            }

        }

        .slider-nav{
            display: flex;
            justify-content: flex-end;
            align-items: center;
            padding-top: 60px;
            .swiper-button-prev{
                margin-right: 55px;
            }
            .swiper-button-prev, .swiper-button-next{
                cursor: pointer;
                &:hover{
                    opacity: 0.5;
                }
            }
            .swiper-button-disabled{
                opacity: 0.4;
            }
        }
    }
}


@media(max-width: 991px){
