.videos{
    margin-top: 100px;
    &__inner{
        gap: 30px;
        padding-top: 50px;
        .video{
            .blue-filter{
                &::before{
                    content: '';
                    position: absolute;
                    width: calc(100% - 40px);
                    height: calc(100% - 40px);
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    background: linear-gradient(0deg, rgba(58, 117, 255, 0.7), rgba(58, 117, 255, 0.7));
                    border-radius: 5px;
                }
            }
            &-preview{
                background: #EFF3FC;
                border-radius: 5px;
                padding: 20px;
                position: relative;
                max-height: 260px;
                height: 100%;
                width: 100%;
                img:not(.play-btn){
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    border-radius: 5px;
                    z-index: 1;
                }
                .play-btn{
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    z-index: 5;
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%, -50%);
                    &:hover{
                        opacity: 0.5;
                    }
                    
                }
            }

            &-author{
                padding-top: 20px;
                font-weight: 600;
                padding-bottom: 5px;
                
               
            }
        }
    }

    
}

@media(max-width: 768px){
    .videos{
        margin-top: 0;

    }
    .videos__inner{
        padding-top: 20px;
        gap: 20px;
        .video{
            .blue-filter{
                &::before{
                    width: calc(100% - 30px);
                    height: calc(100% - 30px);
                }
                
            }
            &-preview{
                padding: 15px;
                min-height: 205px;
                max-height: 205px;
                img{
                    min-height: 173px;
                }
            }

            &-author{
                padding-top: 15px;
                padding-bottom: 5px;
            }
        }
    }
    .videos{
        padding-top: 60px;
    }
}

