.contact__inner{
    padding-top: 30px;
    display: flex;
    .select-wrapper{
        display: flex;
        align-items: center;
        padding-left: 5px;
        svg{
            margin-left: 5px;
        }
    }
    h2{
        font-weight: 600;
        font-size: 21px;
        line-height: 32px;
        color: #000000;
        margin-bottom: 40px;
        display: flex;
        align-items: center;
    }

    select#social-lang{
        font-weight: 600;
        font-size: 21px;
        line-height: 32px;
        color: #3A75FF;
        cursor: pointer;
        option{
            color: black;
            font-size: 16px;
        }
        position: relative;
        &::before{
            content: '1';
            position: absolute;
            top: 0;
            left: 0;
            
        }
        // background: url('../img/blue-arrow.svg');
        // background-size: 11px 9px;
        // background-repeat: no-repeat;
        // background-position: calc(100% - 5px) center;
    }

    .info{
        margin-bottom: 25px;
        max-width: 340px;
        &-title{
           font-weight: bold;
        }

        &-text{
            font-weight: 500;
        }

        &-icons{
            display: grid;
            grid-template-columns: repeat(4, 40px);
            gap: 20px;
            padding-top: 24px;
            a{
                width: 40px;
                height: 40px;
                background: #DBE6FF;
                padding: 5px;
                &:hover{
                    background: #e2eafc;
                }
                img{
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
        }
       
    }

    .contact-info{
        width: 50%;
    }


    .social-lang{
        display: none;
    }

    .social-lang.active{
        display: block;
    }
    
}

