textarea{
    font-family: 'Montserrat';
}

.popup-wrapper{
    position: fixed;
    width: 100%;
    height: 100%;
    overflow-y: auto;
    background: rgba(0,0,0,0.4);
    z-index: 999;
    .popup{
        max-width: 400px;
        width: 100%;
        height: auto;
        padding: 20px;
        background: #F5F8FF;
        border-radius: 5px;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        margin-top: 50px;
        padding-top: 50px;
        .close-popup{
            position: absolute;
            right: 15px;
            top: 10px;
            &:hover{
                opacity: 0.5;
            }
        }
        .input{
            width: 100%;
            margin-bottom: 10px;
            resize: none;
            padding-left: 15px;
        }
        textarea.input::placeholder{
            font-size: 16px;
            font-family: 'Montserrat';
        }
    }

    visibility: hidden;
    opacity: 0;

    .button{
        width: 100%;
    }
}

.popup-wrapper.active{
    visibility: visible;
    opacity: 1;
}

@media(max-width: 400px){
    .popup-wrapper{
        .popup{
            width: 85%;
            margin-top: 20px;
        }
    }

    textarea.input::placeholder{
        font-size: 13px !important;
    }
}